import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DocumentExtractService, PatientInfo, PatientSearchResult } from '../services/document-extract.service';
import { CernerActionService } from '../services/cerner-action.service';

@Component({
  selector: 'app-patient-selection',
  templateUrl: './patient-selection.component.html',
  styleUrls: ['./patient-selection.component.scss']
})
export class PatientSelectionComponent implements OnInit {
  @Input() configLoaded = false;
  @Output() patientSelected = new EventEmitter<PatientInfo>();
  
  patientForm: FormGroup;
  loading = false;
  patientInfo: PatientInfo | null = null;
  patientSearchResults: PatientSearchResult[] = [];
  showPatientSelection = false;
  errorMessage = '';

  constructor(
    private fb: FormBuilder,
    private documentService: DocumentExtractService,
    private cernerActionService: CernerActionService
  ) {
    this.patientForm = this.fb.group({
      fin: ['', [Validators.required, Validators.minLength(1)]]
    });
  }

  ngOnInit(): void {
  }

  /**
   * Handles form submission to search for patients
   */
  onSubmit(): void {
    if (this.patientForm.valid) {
      this.loading = true;
      this.errorMessage = '';
      this.patientInfo = null;
      this.showPatientSelection = false;

      const fin = this.patientForm.get('fin')?.value;

      this.documentService.searchPatients(fin).subscribe({
        next: (patients: PatientSearchResult[]) => {
          this.patientSearchResults = patients;
          this.showPatientSelection = patients.length > 0;
          this.loading = false;
          
          if (patients.length === 0) {
            this.errorMessage = 'No patients found with the provided FIN.';
          }
        },
        error: (error) => {
          this.errorMessage = 'Error searching for patients. Please try again.';
          this.loading = false;
          console.error('Error searching for patients:', error);
        }
      });
    }
  }

  /**
   * Selects a patient and loads their documents
   * @param patient The selected patient
   */
  selectPatient(patient: PatientSearchResult): void {
    this.loading = true;
    this.errorMessage = '';
    this.showPatientSelection = false;

    this.documentService.getPatientDocuments(patient.encntrId).subscribe({
      next: (patientInfo: PatientInfo) => {
        this.patientInfo = patientInfo;
        this.loading = false;
        // Emit the patient information to parent component
        this.patientSelected.emit(patientInfo);
      },
      error: (error) => {
        this.errorMessage = 'Error retrieving patient documents. Please try again.';
        this.loading = false;
        console.error('Error fetching patient documents:', error);
      }
    });
  }

  /**
   * Resets the form and clears patient data
   */
  resetForm(): void {
    this.patientForm.reset();
    this.patientInfo = null;
    this.patientSearchResults = [];
    this.showPatientSelection = false;
    this.errorMessage = '';
  }

  /**
   * Opens the patient chart using Cerner actions
   */
  openChart(): void {
    if (this.patientInfo) {
      this.cernerActionService.openChart(this.patientInfo.personId, this.patientInfo.encntrId);
    }
  }

  /**
   * Opens the MMF Multimedia Manager for the patient using Cerner actions
   */
  openMultimediaManager(): void {
    if (this.patientInfo) {
      this.cernerActionService.openMultimediaManager(this.patientInfo.personId, this.patientInfo.encntrId);
    }
  }
} 