import { NgModule, ErrorHandler, APP_INITIALIZER } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ClinicalOfficeMpageModule, MaterialModule, ErrorHandlerService, ConfigService } from '@clinicaloffice/clinical-office-mpage-core'
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { MatLuxonDateModule, LuxonDateAdapter } from '@angular/material-luxon-adapter';
import { DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE } from '@angular/material/core';
import { MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material/form-field';
import { ComponentVersionComponent } from './component-version/component-version.component';
import { PatientSelectionComponent } from './patient-selection/patient-selection.component';
import { DocumentListComponent } from './document-list/document-list.component';
import { DocumentTrackingComponent } from './document-tracking/document-tracking.component';
import { SystemConfigurationComponent } from './system-configuration/system-configuration.component';
import { NestedObjectViewerComponent } from './system-configuration/nested-object-viewer.component';
import { ExtractDownloadComponent } from './extract-download/extract-download.component';
import { NZ_I18N } from 'ng-zorro-antd/i18n';
import { en_US } from 'ng-zorro-antd/i18n';
import { registerLocaleData } from '@angular/common';
import en from '@angular/common/locales/en';
import { HttpClientModule } from '@angular/common/http';

registerLocaleData(en);

export const configFactory = (configService: ConfigService) => {
  return () => configService.loadConfig();
}

// Custom date formats
export const CUSTOM_DATE_FORMATS = {
  parse: {
    dateInput: ['dd-MMM-yyyy'],
  },
  display: {
    dateInput: 'dd-MMM-yyyy',
    dateTimeLabel: 'dd-MMM-yyyy HH:mm',
    monthYearLabel: 'MMM yyyy',
    dateA11yLabel: 'LL',
    monthYearA11yLabel: 'MMMM yyyy',
  }
}

@NgModule({
  declarations: [
    AppComponent,
    ComponentVersionComponent,
    PatientSelectionComponent,
    DocumentListComponent,
    DocumentTrackingComponent,
    SystemConfigurationComponent,
    NestedObjectViewerComponent,
    ExtractDownloadComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    ClinicalOfficeMpageModule,
    MaterialModule,
    AppRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    MatLuxonDateModule,
    HttpClientModule
    //    MatMomentDateModule
  ],
  providers: [
    {
      provide: APP_INITIALIZER,
      useFactory: configFactory,
      deps: [ConfigService],
      multi: true
    },
    { provide: ErrorHandler, useClass: ErrorHandlerService },
    { provide: DateAdapter, useClass: LuxonDateAdapter, deps: [MAT_DATE_LOCALE] },
//    { provide: MAT_FORM_FIELD_DEFAULT_OPTIONS, useValue: { appearance: 'outline' } },
    {
      provide: MAT_DATE_FORMATS, useValue: CUSTOM_DATE_FORMATS
    },
{ provide: NZ_I18N, useValue: en_US }
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}
